import ShopBtn from '@/components/shopBtn'
import { getRegIntro } from '@/api/user'
export default {
  data () {
    return {
      htmlData: '',
      queryParam: ''
    }
  },
  created () {
    this.queryParam = this.$route.query.levelname
    getRegIntro()
      .then(res => {
        this.$vux.loading.hide()
        if (res.data.errorcode === 0) {
          // 处理数据
          // console.log(res.data.data.content)
          this.htmlData = res.data.data.content
        } else {
          this.$vux.toast.text(res.data.errormsg, 'middle')
        }
      })
      .catch(err => {
        this.$vux.loading.hide()
        console.log(err)
        this.$vux.toast.text('网络异常！', 'middle')
      })
  },
  components: {
    ShopBtn
  },
  methods: {

  }
}



